import * as React from 'react';
import { navigate } from 'gatsby';
import MuiLink from '@mui/material/Link';

interface IProps {
  children: any;
  href: string;
  onClick?: React.MouseEventHandler<HTMLAnchorElement>;
  sx?: any;
}

export const Link = (props: IProps): React.JSX.Element => {
  const { children, href } = props;

  const isInternal: boolean = /^\/(?!\/)/.test(href);
  const isBlank: boolean = !isInternal;

  const onClick: React.MouseEventHandler<HTMLAnchorElement> = (
    e: React.MouseEvent<HTMLAnchorElement>
  ) => {
    window.localStorage.setItem('scrollY', String(0));

    if (!isBlank) {
      e.preventDefault();
      navigate(href);
    }
  };

  return (
    <MuiLink
      {...props}
      href={href}
      target={isBlank ? '_blank' : '_self'}
      rel={isBlank ? 'noopener noreferrer' : ''}
      onClick={props.onClick ? props.onClick : onClick}
    >
      {children}
    </MuiLink>
  );
};
